<template>
  <Icon
    :class="{ 'pin-check_active': value }"
    class="pin-check"
    name="pin"
    @click="toggle"
  />
</template>

<script>
import { useModel } from '@/shared/utils/useModel'
import Icon from '@/shared/components/Icon/Icon'

export default {
  name: 'PinCheck',
  components: { Icon },
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      type: Boolean,
      required: true
    }
  },
  setup (props, { emit }) {
    const { value } = useModel(props, emit)

    const toggle = () => {
      value.value = !value.value
    }

    return {
      value,
      toggle
    }
  }
}
</script>

<style lang="scss">
.pin-check {
  opacity: 0.2;
  cursor: pointer;

  svg {
    path {
      //stroke: map-get($colors, "default");
      //fill: map-get($colors, "default");
    }
  }
  &_active {
    opacity: 1;
    svg {
      path {
        stroke: map-get($colors, "primary");
        fill: map-get($colors, "primary");
      }
    }
  }
}
</style>
